import React from "react";
import { Col, Row } from "react-bootstrap";
import {
  DiJavascript1,
  DiReact,
  DiNodejs,
  DiMongodb,
  DiHtml5,
  DiCss3Full
} from "react-icons/di";

import {
  SiNextdotjs,
  SiExpress,
  SiRedux,
  SiTypescript,
} from "react-icons/si";

const frontendStacks = [
  {
    icon: <DiHtml5/>,
    label: "HTML",
  },
  {
    icon: <DiCss3Full />,
    label: "CSS",
  },
  {
    icon: <DiJavascript1 />,
    label: "JavaScript",
  },
  {
    icon: <DiReact />,
    label: "React",
  },
  {
    icon: <SiRedux />,
    label: "Redux",
  },
  {
    icon: <SiNextdotjs />,
    label: "NextJS",
  },
  {
    icon: <SiTypescript />,
    label: "TypeScript",
  }
];

const backendStacks = [
  {
    icon: <DiNodejs />,
    label: "Node",
  },
  {
    icon: <SiExpress />,
    label: "Express",
  },
  {
    icon: <DiMongodb />,
    label: "MongoDB",
  }
];

function Techstack() {
  return (
    <>
      <h1 className="project-heading">
        Frontend
      </h1>
      <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
        {
          frontendStacks.map(stack => (
            <Col key={stack.label} xs={4} md={2} className="tech-icons">
              <div className="skill glass light">
                {stack.icon}
                <p>{stack.label}</p>
              </div>
            </Col>
          ))
        }
      </Row>
      
      <h1 className="project-heading">
        Backend
      </h1>
      <Row style={{ justifyContent: "center", paddingBottom: "50px" }}>
        {
          backendStacks.map(stack => (
            <Col key={stack.label} xs={4} md={2} className="tech-icons">
              <div className="skill glass light">
                {stack.icon}
                <p>{stack.label}</p>
              </div>
            </Col>
          ))
        }
      </Row>
    </>
  );
}

export default Techstack;
